import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';

import {ProjectService} from '../services/project.service';
import {Project, Projects} from '../models/response.model';
import {NotificationService} from "../services/notification.service";


@Component({
  selector: 'oty-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {
  projectList: Projects[];
  projectListOriginal: Projects[];
  isShowDeleteArea = false;
  isOverDelete = false;
  @ViewChild('searchInput') searchInput: ElementRef;
  constructor(private projectService: ProjectService,
              private notificationService: NotificationService) {
  }

  ngOnInit() {
    this.loadProjects();
  }

  private loadProjects() {
    this.projectService.getProjects()
      .subscribe((projects: Projects[]) => {
        this.projectList = projects;
        this.projectListOriginal = projects;
      });
  }

  onSearchChange(value) {
    this.projectList = this.projectListOriginal.filter((item) => {
      return item.projectName.toLowerCase().includes(value.toLowerCase());
    });
  }

  getRandomProjectIcon() {
    let max = 6,
      n = Math.floor(Math.random() * Math.floor(max)) + 1;
    return n;
  }

  onDragStart(event, project: Projects){
    this.isShowDeleteArea = true;
    event.dataTransfer.setData('projectSelected', JSON.stringify(project));
  }

  onDragEnd(event){
    this.isShowDeleteArea = false;
  }

  deleteProject(event){
    const project:Projects = JSON.parse(event.dataTransfer.getData('projectSelected'));
    this.projectService
      .deleteProject(project.projectId)
      .subscribe((res:any)=>{
        if(res.success === true){
          this.notificationService.showNotification('success', `${project.projectName} deleted`);
        }
        this.loadProjects();
        this.clearSearchInput();
        this.isOverDelete = false;
      });
  }

  clearSearchInput(){
    this.searchInput.nativeElement.value = '';
  }


}



